@use 'base/variables' as v;
@use 'base/mixins' as m;

.alerta {
  padding: 1.5rem 1.5rem 1.5rem 3rem;
  text-transform: uppercase;
  margin: 1rem 0;
  font-size: 1.2rem;
  font-weight: bold;
  &__error {
    background-color: lighten($color: v.$rojo, $amount: 60%);
    border-left: 0.5rem solid v.$rojo;
    color: v.$rojo;
  }

  &__exito {
    background-color: lighten($color: v.$verde, $amount: 60%);
    border-left: 0.5rem solid v.$verde;
    color: darken(v.$verde, 20%);
  }
}
